import Video from "../../Elements/Media/Video";
//import Image from "./Image";
import Image from "../../Elements/Media/Image";
const Media = ({ itemData, wallID, ownerId, wall, size, isCover }) => {
  const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
  const isYoutubePost = (itemData.network.id === 7) ? true : false
  const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
  const multiImagesClass = (multiImages && mediaType) ? 'tb_hc_multi_images_ico_bottom' : 'tb_hc_multi_images_ico'
  const isAudio = itemData.isAudio === 1 ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  return <div className="tb_hc_media_wrap">
    {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : null}
    {mediaType && !autoPlay ? isYoutubePost ?
      <div className="tb_hc_youtube_ico tb__icon tb-youtube-default">
        <div className="tb_color_icon__ tb_d_youtube_path_1">
          <div></div>
        </div>
        <div className="tb_color_icon__ tb_d_youtube_path_2">
          <div></div>
        </div>
      </div>
      :
      isAudio ?
        <div className="tb_hc_audio_ico tb__icon tb-audio"><div></div></div>
        :
        <div className="tb_hc_video_ico tb__icon tb-video"><div></div></div>
      : ''}

    {autoPlay ?
      <Video VideoClass="tb_hc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} wall={wall} size={size} isCover={isCover} />
      :
      <Image ImageClass="tb_hc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} />
    }
  </div>
}
export default Media