import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import { PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { suiteTrack } from "../../../../actions/api";

const trim_content_length = 140;
export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.postTrack = React.createRef();
  }
  componentDidMount() {
    if (suiteTrack) themeImpressionTracking(this.postTrack)
  }

  render() {
    const { itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall } = this.props;

    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
    const rating = itemData.rating && (itemData.rating > 0 && itemData.network.id !== 29) ? true : false;

    const cardStyle = {
      backgroundColor: ThemeRule.cardColor,
    };
    const contentClass = (itemData.rating > 0) ? 'tb_hc_rating_content' : 'tb_hc_content';
    const noMediaClass = (itemData.type === 1) ? 'tb_hc_text_post' : '';
    const textDecorate = (itemData.type === 1) && (personalization.textDecorate) ? 'tb_hc_text_decoration' : '';
    const ctaActive = personalization.cta.ctaStatus || itemData.cta.status ? true : false;
    const postTextContent = ThemeRule.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content

    return (
      <div item-id={itemData.id} id={`tb-hc-post-${itemData.id}`} className="tb_hc_post_wrapper" ref={this.postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId}>
        <div className="tb_hc_post_in" style={cardStyle} onClick={clickToShowPopUp(itemIndex, itemData)}>
          {mediaType ? <div className="tb_hc_post_media_wrapp">
            <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} size={personalization.trimcontent ? 100 : false} isCover={personalization.trimcontent ? true : false} />
          </div> : null}
          {rating ? <div className={`tb_hc_rating__`} >
            <Rating rating={itemData.rating} network={itemData.network} />
          </div> : null}

          <div className={`tb_hc_contant_wrapper ${noMediaClass}`}>
            <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} rating={itemData.rating} />
            {ctaActive ? <div className="tb_hc_post_cta" onClick={(e) => { e.stopPropagation() }}>
              <CTAButton ctaClass={'tb_hc_post_cta_btn'} cta={personalization.cta} postCta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div>
              : ''}
            {!itemData.hideContent ?
              <div className={`tb_hc_content_wrapper__`}>
                <PostContent key={itemData.id} contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={itemData.font} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} />
              </div>
              : null}

          </div>
          {ThemeRule.socialAction ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={ThemeRule.themeId} actionClass={'tb_hc_social_action__'} /> : ''}
        </div>
      </div>
    );
  }
}
